<template>
	<view>

		<uni-nav-bar @clickLeft="backCourseList" left-icon="back" :border="false" title="套餐详情" statusBar>
			<view slot="right" @click="share">
				<view :animation="animationData" class="sub_jifen">赚积分</view>
				<image class="pinglun-icon" src="../../static/share_.png"></image>
			</view>
		</uni-nav-bar>

		<!-- 直播背景 -->
		<view class="live_course_bg_wrap">
			<image class="live_course_img" :src="live_course_bg" mode="aspectFill"></image>
		</view>
		<view class="course-bottom">

			<scroll-view style="width: 100%; " scroll-y :style="'height:' + scrollH+'rpx;'">
				<!-- 标题标签区域 -->
				<view>
					<view style="font-weight: bold; font-size: medium;">{{liveInfo.name}}</view>
					<view>{{liveInfo.des}}</view>
				</view>

				<!-- 价格时间学习人数信息 -->
				<view class="price-time-stunum">
					<template v-if="isbuy == 0">
						<text class="rmb_icon">{{ '¥'+liveInfo.price}}</text>
					</template>
					<template v-else>
						<text class="rmb_icon">已付费</text>
					</template>
					<text class="time-wrap">{{liveInfo.nums +'课程'}}</text>
					<text class="stunum-wrap">{{liveInfo.views}}人学习</text>
				</view>
				<view class="new_line"></view>
				<view class="about-title alive-title">包含课程</view>
				<view @click="viewCourseInfo(item.id,item.paytype)" class="live-list" v-for="(item, index) in course" :key="index">
					<view class="shopdetail">
						<view class="live-list-img-wrap">
							<image class="live-list-img" :src="item.thumb" mode="aspectFill"></image>
							<text class="course-title-icon">课程</text>
						</view>

						<view class="live-list-info">
							<view class="live-c-title">{{item.name}}</view>
							<view class="live-status">
								{{item.lesson}}
							</view>

							<view class="live-teacher-price">
								<view class="live-teacher-price">
									<image class="live-teacher-avatar" :src="item.avatar" mode="aspectFill"></image>
									<text class="teacher-name">{{item.user_nickname}}</text>
								</view>
								<view class="price-wrap">
									<text class="numPrice">
										{{'¥' + item.payval}}
									</text>
								</view>
							</view>
						</view>

					</view>
				</view>
				<view class="about-live-wrap">
					<text class="about-title alive-title">套餐介绍</text>
					<view class="alive-info">
						<rich-text :nodes="liveInfo.content"></rich-text>
					</view>
				</view>
			</scroll-view>

			<template v-if="isbuy == 0">
				<view @click="enterpaylive">
					<template v-if="isbuy == 0">
						<view class="carline"></view>

						<view class="carView">
							<view class="car" @click="shopcar">
								<view class="new_gouwuche">
									<image class="gowucheimage" src="../../static/gouwuche.png" mode="aspectFit">
									</image>
									<template v-if="nums != 0">
										<view class="gouwunums">
											<view class="carnums">{{nums}}</view>
										</view>
									</template>
								</view>
								<view class="cartitle">购物车</view>
							</view>
							<template v-if="liveInfo.iscart == 0">
								<view class="joincar" @tap="doAddCar">加入购物车</view>
							</template>
							<template v-else>
								<view class="joincar2">已加入购物车</view>
							</template>
							<view class="buy" @tap="buy">立即购买</view>
						</view>

					</template>
				</view>
			</template>
			<template v-else>
				<!-- <view @click="enterContent()" class="inlive-btn">
					查看详情
				</view> -->
			</template>
		</view>
		<view :hidden="userpasswordkHidden" class="popup_content">
			<view class="popup_title">输入密码</view>
			<view class="popup_textarea_item">
				<input type="text" class="popup_textarea" value="" v-model="passwordcontent" placeholder="输入密码" />
				<view @click="submitPassword" class="buttons">
					<text class="popup_button">确定</text>
				</view>
			</view>
		</view>
		<view class="popup_overlay" :hidden="userpasswordkHidden" @click="hideDiv()"></view>

		<view class="extension-zhe" v-if="isHidden == 0">
			<view class="extension-zhe-content">
				<view class="extension-zhe-content-tips">
					分享方式
					<image @click="close" class="extension-zhe-content-tips-img" src="../../static/ming-pop/close.png"></image>
				</view>
				<view class="extension-zhe-content-type">

					<!-- 	<view class="extension-zhe-content-type-li" @click="qqshare">
						<image class="extension-zhe-content-type-li-img" src="../../static/fuzhilianjie.png"></image>
						<view class="extension-zhe-content-type-li-text">复制链接</view>
					</view> -->

					<!-- <view class="extension-zhe-content-type-li" @click="wechatshare">
						<image class="extension-zhe-content-type-li-img" src="/static/images/login_wx.png"></image>
						<view class="extension-zhe-content-type-li-text">微信好友</view>
					</view> -->


					<view class="extension-zhe-content-type-li" @click="haibaoshare">
						<image class="extension-zhe-content-type-li-img" src="../../static/tuiguang_haibao.png"></image>
						<view class="extension-zhe-content-type-li-text">海报分享</view>
					</view>

				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';


	const app = getApp();

	export default {
		components: {
			uniNavBar,
		},
		data() {
			return {
				scrollH: 0,
				live_course_bg: '', //直播背景图片
				liveInfo: {},
				isbuy: 0,
				userpasswordkHidden: true, // 默认隐藏
				feedbackpassword: '', // 输入密码
				passwordcontent: '',
				getcourseid: '',
				nums: '',
				courseid: '', //内容id
				stararr: [1, 2, 3, 4, 5],
				webview: '',
				course: {},
				INFO: [],
				animationData: [],
				isHidden: 1
			}
		},
		onReady: function() {

			var that = this;
			uni.getSystemInfo({
				success: function(res) {

					that.scrollH = res.windowHeight * 750 / res.windowWidth - 600;
					// #ifdef MP-WEIXIN
					that.scrollH = res.windowHeight * 750 / res.windowWidth - 750;
					// #endif
				}
			});
			// let uid = app.globalData.userinfo.id;
			// let token = app.globalData.userinfo.token;
			// this.webview = getApp().globalData.site_h5url + 'appapi/course/info?uid=' + uid + '&token=' + token + '&courseid=' +
			// 	this.getcourseid;
			// console.log(this.webview);
			this.getnums();

			var num = -7;
			setInterval(() => {
				var animation = uni.createAnimation({
					duration: 400,
					delay: 0,
				})
				if (num == -7) {
					num = 0;
				} else if (num == 0) {
					num = -7;
				}
				animation.translateX(num).step()
				this.animationData = animation.export()
			}, 400);
		},
		onShow: function() {
			setTimeout(() => {
				this.getContentInfo(this.getcourseid);
				this.getnums();
			}, 300);

			this.isHidden = 1;
		},

		onLoad(option) {
			this.getContentInfo(option.courseid);
			this.getcourseid = option.courseid;
			console.log("courseid = " + option.courseid);
			this.courseid = option.courseid;
		},
		methods: {
			setLogin() {
				// 没有登录则弹窗登录
				if(app.globalData.userinfo == '') {
					uni.showModal({
						title: '请先登录你的账号',
						content: '',
						showCancel: true,
						cancelText: '取消',
						confirmText: '立即登录',
						confirmColor: '#38DAA6',
						success: res => {
							if (res.confirm) {
								uni.navigateTo({
									url: '../../../pages/login/login'
								})
							}
						},
						fail: () => {},
						complete: () => {}
					});
					return false;
				}
				return true;
			},
			share() {
				let isLogin = this.setLogin();
				if(!isLogin) {
					return;
				}
				console.log('分享');
				this.isHidden = 0;
			},
			haibaoshare() {
				uni.navigateTo({
					url: '../../../pages/mineExtension/extension_poster?courseid=' + this.getcourseid + '&type=2',

				});
			},
			wechatshare() {

			},
			qqshare() {


			},
			//关闭推广
			close() {
				this.isHidden = 1;
			},
			viewCourseInfo(contentCourseId, contentCoursetype) {
				let isLogin = this.setLogin();
				if(!isLogin) {
					return;
				}
				
				if (getApp().globalData.userinfo == '') {
					uni.navigateTo({
						url: '../../../pages/login/login'
					})
					return;
				}
				uni.navigateTo({
					url: '../../../pages/courseinfo/courseinfo?courseid=' + contentCourseId + '&paytype=' + contentCoursetype,

				});

			},
			getnums() {
				let gData = app.globalData;
				uni.request({
					url: gData.site_url + 'Cart.GetNums',
					method: 'POST',
					data: {
						'uid': gData.userinfo.id,
						'token': gData.userinfo.token,
					},
					success: res => {
						this.nums = res.data.data.info[0].nums;
					},
				});
			},
			backCourseList() {
				uni.navigateBack({
					delta: 1
				});
			},
			enterpasslive() {
				// 显示输入弹出框
				this.userpasswordkHidden = false;

			},
			//提交密码 passwordcontent
			submitPassword() {
				uni.showLoading({
					title: '......',
					icon: 'none',
				});

				console.log("提交密码 getcourseid =" + this.getcourseid);
				var passwordcontent = this.passwordcontent;
				this.userpasswordkHidden = true;
				let gData = app.globalData;

				uni.request({
					url: gData.site_url + 'Course.CheckPass',
					method: 'POST',
					data: {
						"pass": passwordcontent,
						"courseid": this.getcourseid,
						'uid': gData.userinfo.id,
						'token': gData.userinfo.token
					},
					success: res => {
						uni.hideLoading();
						console.log(res);
						uni.showToast({
							title: res.data.data.msg,
							icon: 'none',
						})
						if (res.data.data.code == 0) {
							console.log("enterlive");
							this.enterContent();
						} else {

						}
					},
					fail: () => {
						uni.hideLoading();
					},
					complete: () => {}
				});

			},
			// 获取套餐详情
			getContentInfo(courseid) {
				let gData = app.globalData;
				uni.request({
					url: gData.site_url + 'Package.GetInfo',
					method: 'GET',
					data: {
						'uid': 0, //uniapp uid和token不传, 用户可以查看详情, 但没法做任何操作 
						'token': '',
						'packageid': courseid
					},
					success: res => {
						if (res.data.data.code == 700) {
							uni.navigateTo({
								url: '../../../pages/login/login',
								success: res => {},
								fail: () => {},
								complete: () => {}
							});
							return;
						}
						this.INFO = res.data.data.info;
						let info = res.data.data.info[0];
						this.live_course_bg = info.thumb ? info.thumb : '';
						this.liveInfo = info;
						this.isbuy = parseInt(info.isbuy);
						this.course = info.courses;
						// console.log(JSON.parse(JSON.stringify(res)));
					},
				});
			},
			enterpaylive() {

			},
			// 查看套餐详情
			enterContent() {


			},

			//加入购物车
			doAddCar() {
				let isLogin = this.setLogin();
				if(!isLogin) {
					return;
				}
				
				let gData = app.globalData;
				var that = this;
				uni.request({
					url: gData.site_url + 'Cart.Add',
					method: 'POST',
					data: {
						"type": "1",
						"typeid": this.getcourseid,
						'uid': gData.userinfo.id,
						'token': gData.userinfo.token,
					},
					success: res => {
						console.log(res);
						this.getContentInfo(this.getcourseid);
						this.getnums();
						uni.showToast({
							title: res.data.data.msg
						})

					},
				});
			},
			shopcar() {
				console.log('前往购物车');
				let isLogin = this.setLogin();
				if(!isLogin) {
					return;
				}
				
				uni.navigateTo({
					url: '../../../pages/shop-car/shop-car',
				});
			},
			//立即购买
			buy() {
				let isLogin = this.setLogin();
				if(!isLogin) {
					return;
				}
					
				uni.navigateTo({
					url: '../../../pages/pay/pay?info=' + encodeURIComponent(JSON.stringify(this.INFO)),

				});
			}

		}
	}
</script>

<style>
	/* 课程详情页公共样式 */
	@import url("/static/css/course_info/course_info.css");
	@import url("/static/common/css/pingjia.css");
page{
	width: 100%;
	height: 100%;
	overflow: hidden;
}
	.sub_jifen {
		position: absolute;
		right: 60rpx;
		margin-top: -10rpx;
		color: #FFFFFF;
		text-align: center;
		padding-bottom: 5rpx;
		width: 80rpx;
		height: 30rpx;
		border-radius: 15rpx;
		font-size: 20rpx;
		background-color: #FF3939;
	}

	.new_gouwuche {
		margin-top: 10rpx;
		position: relative;
		width: 80rpx;
		height: 80rpx;
	}

	.gouwunums {
		position: absolute;
		background-color: #DC2929;
		width: 26rpx;
		height: 26rpx;
		border-radius: 13rpx;
		margin-left: 40rpx;
		margin-top: 10rpx;
	}

	.gowucheimage {
		position: absolute;
		width: 70upx;
		height: 50upx;
		left: 0rpx;
		margin-top: 10rpx;
	}

	.carView {
		display: flex;
		flex-direction: row;
		bottom: 0upx;
		background-color: #FFFFFF;
		height: 110upx;
		position: fixed;
		width: 100%;
	}

	.carnums {
		width: 26rpx;
		height: 26rpx;
		font-size: 20rpx;
		color: #FFFFFF;
		text-align: center;
		line-height: 26rpx;
	}

	.car {
		width: 30%;
		align-items: center;
		text-align: center;
		display: flex;
		flex-direction: column;

	}

	.cartitle {
		color: #333333;
		font-size: small;
	}


	.linshi {
		display: flex;
		flex-direction: row;
	}

	.course-bottom {
		padding-left: 20rpx;

	}

	.popup_overlay {
		position: fixed;
		top: 0%;
		left: 0%;
		width: 100%;
		height: 100%;
		background-color: black;
		z-index: 1001;
		-moz-opacity: 0.8;
		opacity: .80;
		filter: alpha(opacity=88);
	}

	.popup_content {
		position: fixed;
		top: 50%;
		left: 50%;
		width: 520rpx;
		height: 250rpx;
		margin-left: -270rpx;
		margin-top: -270rpx;
		border: 10px solid white;
		background-color: white;
		z-index: 1002;
		overflow: auto;
	}

	.popup_title {
		width: 480rpx;
		text-align: center;
		font-size: 32rpx;
	}

	.popup_textarea_item {
		padding-top: 5rpx;
		height: 80rpx;
		width: 440rpx;
		background-color: #F1F1F1;
		margin-top: 20rpx;
		margin-left: 20rpx;
		padding-top: 25rpx;
	}

	.popup_textarea {
		width: 410rpx;
		font-size: 26rpx;
		margin-left: 20rpx;
	}

	.popup_button {
		color: #000000;
	}

	.buttons {
		text-align: center;
		font-size: 32rpx;
		margin-top: 40rpx;
	}


	.joincar {
		text-align: center;
		width: 40%;
		color: #FFFFFF;
		background-color: #FFAC4B;
		font-size: larger;
		padding-top: 25upx;
	}

	.joincar2 {
		text-align: center;
		width: 40%;
		color: #FFFFFF;
		background-color: #CCCCCC;
		font-size: larger;
		padding-top: 25upx;
	}

	.buy {
		padding-top: 25upx;
		text-align: center;
		font-size: larger;
		width: 40%;
		color: #FFFFFF;
		background-color: #FF623E;
	}

	.liveinfo-wrap {
		padding-top: 2rpx;
		min-height: 1500rpx;
		background-color: #FFFFFF;
	}

	.live-list {
		width: 100%;
		height: 190rpx;
		/* 		margin: 30rpx auto;
	*/
		/* padding-top: 20rpx; */
		/* box-shadow: 0rpx 0rpx 4rpx 2rpx rgba(0, 0, 0, 0.1); */
		/* border-radius: 8rpx; */
		background-color: #FFFFFF;
		display: flex;
		flex-direction: row;
	}

	.shopdetail {
		display: flex;
		flex-direction: row;
		width: 100%;
		margin-left: 10rpx;

	}

	.live-c-title {
		line-height: 35rpx;
		font-weight: bold;
		/* height: 80rpx; */


	}

	.live-list-img-wrap {
		position: relative;
		width: 180rpx;
		height: 160rpx;
		float: left;
		margin-right: 6rpx;
	}

	.live-list-img {
		width: 100%;
		height: 100%;
		border-radius: 8rpx;
	}

	.course-title-icon {
		position: absolute;
		left: 0rpx;
		top: 2rpx;
		width: 80rpx;
		height: 40rpx;
		line-height: 40rpx;
		text-align: center;
		border-radius: 16rpx 0 16rpx 0;
		background-color: rgba(0, 0, 0, 0.6);
		font-size: small;
		color: #DBDBDB;
	}


	.live-list-info {
		float: left;
		width: 65%;
		height: 140rpx;
		/* margin-left: 220rpx; */
	}

	.live-teacher-avatar {
		width: 35rpx;
		height: 35rpx;
		border-radius: 50%;

	}

	.live-status {
		font-size: 20rpx;
		padding-top: 10rpx;
		color: #969696;
	}

	.living-status {
		padding-top: 10rpx;
		color: #64D3AD;
	}

	/* 价格 */
	.live-teacher-price {
		display: flex;

	}

	.price-wrap {
		display: flex;
		color: #64D3AD;
		position: absolute;
		right: 0px;
		padding-right: 50rpx;
	}

	.free {
		color: #64D3AD;
	}

	.numPrice {
		color: #FF1B20;

	}

	.pass {
		color: #4385FF;
	}

	.teacher-name {

		margin-left: 15rpx;
		font-size: 20rpx;
		color: #323232;
		width: auto;
	}
</style>
